// src/pages/api/products/index.js
import CatalogService from "services/catalogService.js";
import dbConnect from "lib/dbConnect.js";

/**
 * API Handler: /api/products
 *
 * GET -> search/list products (q,page,limit)
 * POST -> create product
 */
export default async function handler(req, res) {
  try {
    await dbConnect();
  } catch (err) {
    console.error("DB connect failed:", err);
    return res.status(500).json({ error: "Database connection failed" });
  }

  if (req.method === "GET") {
    try {
      const { q = "", page = 1, limit = 20, inStockOnly = "false" } = req.query;
      const result = await CatalogService.searchProducts({
        q,
        page: Number(page),
        limit: Number(limit),
        inStockOnly: inStockOnly === "true" || inStockOnly === true
      });
      return res.status(200).json(result);
    } catch (err) {
      console.error("GET /api/products error:", err);
      return res.status(500).json({ error: "Failed to fetch products" });
    }
  }

  if (req.method === "POST") {
    try {
      // optional: validate with DomainProduct.validate
      const created = await CatalogService.createProduct(req.body);
      // created is a DomainProduct instance; convert to plain object for response
      return res.status(201).json(created && created.toObject ? created.toObject() : created);
    } catch (err) {
      console.error("POST /api/products error:", err);
      return res.status(400).json({ error: err.message });
    }
  }

  res.setHeader("Allow", ["GET", "POST"]);
  return res.status(405).end();
}
